.NavbarItems{
  background-color: rgb(20, 2, 2);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;

}
.navbar-logo{
  color: white;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
}
.fa-react{
  margin-left: 0.5rem;
  font-size: 1.6rem;
}
.nav-menu{
  display: grid;
  grid-template-columns: repeat(5,auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}
.nav-links{
  color: rgb(247, 251, 255);
  text-decoration: none;
  padding: 0.5rem 1rem;
  font-size: 15px;
  font-style: none;
}
.nav-links:hover{
  background-color: rgb(103, 103, 214);
  border-radius: 4px;
  transition: all 0.2s ease-out;
}
.fa-bars{
  color: azure;
}
.nav-links-mobile{
  display: none;
}
.menu-icon{
  display: none;
}
@media screen and (max-width:960px){
  .NavbarItems{
    position: relative;
  }
  .nav-menu{
display: flex;
flex-direction: column;
width: 100%;
height: 500px;
position:absolute ;

top: 80px;
left: -100%;
opacity: 1;
transition: all 0.5s ease;
  }
  .nav-menu.active{
    background: blue;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-links{
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }
  .nav-links:hover{
    background-color: rgb(139, 139, 223);
    border-radius: 0;
  }
  .navbar-logo{
    position: absolute;
    top:0;
    left: 0;
    transform: translate(25%,50%);
  }
  .menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%,60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
  .fa-times{
    color: rgb(216, 226, 228);
    font-size: 2rem;
  }
  .nav-links-mobile{
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: rgb(182, 142, 128);
    color: rgb(241, 241, 239);
    font-size: 1.5rem;
  }
  .nav-links-mobile:hover{
    background: white;
    color: rgb(250, 245, 241);
    transition: 250ms;

  }
  Button{
    display: none;
  }
}