:root{
    --primary:rgb(18, 175, 202);
}
.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;

}
.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: white;
    color: rgb(47, 35, 223);
    transition: 250ms;
}
.btn-primary{
    background-color: var(--primary);
}
.btn--outline{
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}